import { useState } from "react";
import LogoBlack from "../../assets/logo_black.svg";
import { ReactComponent as LogoBlackSplit1 } from "../../assets/logo_black_split_1.svg";
import { ReactComponent as LogoBlackSplit2 } from "../../assets/logo_black_split_2.svg";
import { ReactComponent as LogoBlackSplit3 } from "../../assets/logo_black_split_3.svg";

const Navbar = () => {
  const [hovered, setHovered] = useState(false);

  return (
    <nav className="p-4 static top-0 w-full bg-red-500">
      <div className="flex w-full items-center justify-center relative text-black">
        <img
          src={LogoBlack}
          alt="Logo Black"
          className={`h-12 w-fit transition-opacity duration-300 ${
            hovered ? "pointer-events-none opacity-0" : "opacity-100"
          }`}
          onMouseEnter={() => {
            if (window.innerWidth > 768) {
              setHovered(true);
            }
          }}
        />
        <div
          className={`flex items-center justify-center h-12 absolute left-1/2 top-0 -translate-x-1/2 transition-opacity duration-300 ${
            hovered ? "opacity-100" : "opacity-0 pointer-events-none"
          }`}
          style={{ zIndex: 3 }}
          onMouseLeave={() => setHovered(false)}
        >
          <LogoBlackSplit1 className="h-5 w-fit" />
          <LogoBlackSplit2 className="h-5 w-fit" />
          <LogoBlackSplit3 className="h-5 w-fit" />
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
